import React from 'react';
import Preloader from '../../common/Preloader/Preloader';
import s from './ProfileInfo.module.css';
import ProfileStatusWithHoocks from './ProfileStatusWithHoocks';

const ProfileInfo = (props) => {

    if (!props.profile) {
        return <Preloader />
    }
    return (
        <div>
            <div className={s.descriptionBlock}>
                <div>
                    <img alt="11" className={s.ava} src={props.profile.photos.large} />
                </div>
                <li className={s.des} >
                    <ul className={s.des_item}>{props.profile.fullName}</ul>
                    <ul><ProfileStatusWithHoocks status={props.status} updateStatus={props.updateStatus} /></ul>
                    <ul className={s.des_item}> : {props.profile.aboutMe}</ul>
                    <ul className={s.des_item}> : {props.profile.lookingForAJobDescription}.</ul>
                    <ul className={s.des_item}>{props.profile.lookingForAJob !== false ? " " : "  "}</ul>
                </li>
            </div>
        </div>
    )
}

export default ProfileInfo;

